.footer {
    @include flexAlignment();
    width: 100%;
    height: 63px;
    background-color: #131313;
    box-sizing: border-box;
    &__content {
        @include flexAlignment();
    }
    &__address {
        color: #ffffff;
        font-family: "Ubuntu-Regular";
        font-size: 14px;
        line-height: 20px;
    }
    &__phone {
        color: #e45028;
        font-family: "Ubuntu-Bold";
        font-size: 14px;
        line-height: 20px;
    }
    &__social-button {
        @include flexAlignment();
        width: 144px;
    }
    &__social-button > div, a {
        @include flexAlignment(flex, center, center);
        width: 27px;
        height: 24px;
    }
    &__social-button img {
        width: auto;
        height: 22px;
    }
    &__social-button-vk img {
        height: 14px;
    }
}
p {
    margin: 0;
}